.department-list {
  margin: 2rem 0;
  border: 1px solid #adadad;
  border-radius: 8px;
  font-size: 14px;
  height: 46px;
  padding: 10px;
  border: 1px solid rgb(153, 169, 68);
}

.department-error {
  text-align: left;
  color: red;
}

@media (max-width: 440px) {
  .department-list {
    margin: 2.6rem;
  }
}

@media (max-width: 435px) {
  .department-list {
    margin: 3.5rem;
  }
}

@media (max-width: 360px) {
  .department-list {
    margin: 0 1.5rem;
  }
}
