.country-list select {
  margin: 2rem 0;
  border: 1px solid #adadad;
  border-radius: 8px;
  font-size: 14px;
  height: 46px;
  padding: 10px;
  border: 1px solid rgb(153, 169, 68);
}

@media (max-width: 440px) {
  .country-list {
    margin: 0 1.8rem;
  }
}

@media (max-width: 435px) {
  .country-list {
    margin: 0 2.4rem;
  }
}

@media (max-width: 360px) {
  .country-list {
    margin: 0 0.8rem;
  }
  .country-list select {
    width: 104%;
  }
}
